<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="dialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="roleList" border>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scopes">
            <!--  -->
            <el-row
              v-for="(item, index) in scopes.row.children"
              :key="index"
              :class="['topline', index == 0 ? 'bottomline' : '', 'vcenter']"
            >
              <el-col :span="5">
                <el-tag closable @close="removeRight(scopes.row, item.id)">
                  {{ item.authName }}
                </el-tag>
                <i class="el-icon-arrow-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  v-for="(item1, index1) in item.children"
                  :key="index1"
                  :class="[index1 == 0 ? '' : 'topline', 'vcenter']"
                >
                  <!--  -->
                  <el-col :span="8">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRight(scopes.row, item1.id)"
                      >{{ item1.authName }}</el-tag
                    >
                    <i class="el-icon-arrow-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="16">
                    <el-tag
                      closable
                      @close="removeRight(scopes.row, item2.id)"
                      type="warning"
                      v-for="(item2, index2) in item1.children"
                      :key="index2"
                      >{{ item2.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scopes">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-search"
              @click="geteditrole(scopes.row.id)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleterole(scopes.row.id)"
              ></el-button
            >
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scopes.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="addreset"
    >
      <el-form
        :model="addrole"
        :rules="addrolerules"
        ref="addroleref"
        label-width="100px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="addrole.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addrole.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="Toaddrole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editdialogVisible"
      width="50%"
      @close="editreset"
    >
      <el-form
        :model="editrole"
        :rules="addrolerules"
        ref="editroleref"
        label-width="100px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="editrole.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editrole.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="Toeditrole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="getRightdialogVisible"
      width="50%"
      @close='keyArry=[]'
    >
      <el-tree
        :data="getroleRight"
        :props="treeProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys='keyArry'
        ref='Thetree'
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="getRightdialogVisible = false"> </el-button>
        <el-button type="primary" @click="TochangeRight"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //tree
      getroleRight: [],
      //
      roleList: [],
      //
      dialogVisible: false,
      //
      editdialogVisible: false,
      //
      getRightdialogVisible: false,
      //
      addrole: {
        roleName: '',
        roleDesc: ''
      },
      //
      editrole: {},
      addrolerules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: false },
          { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
        ]
      },
      //
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      //id
      keyArry:[],
      // 
      role:''
    }
  },
  created() {
    this.getroleList()
  },
  methods: {
    async getroleList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.roleList = res.data
    },
    //
    Toaddrole() {
      this.$refs.addroleref.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.post('roles', this.addrole)
        console.log(res)
        if (res.meta.status != 201) return this.$message.error('')
        this.$message.success('')
        this.dialogVisible = false
        this.getroleList()
      })
    },
    //
    addreset() {
      this.$refs.addroleref.resetFields()
    },
    //
    editreset() {
      this.$refs.editroleref.resetFields()
    },
    //id
    async geteditrole(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status != 200) return this.$message.error('')
      this.editrole = res.data
      this.editdialogVisible = true
    },
    //
    Toeditrole() {
      this.$refs.editroleref.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.put(
          'roles/' + this.editrole.roleId,
          {
            roleName: this.editrole.roleName,
            roleDesc: this.editrole.roleDesc
          }
        )
        console.log(res)
        if (res.meta.status != 200) return this.$message.error('')
        this.editdialogVisible = false
        this.getroleList()
        this.$message.success('')
      })
    },
    //
    async deleterole(id) {
      const deleteR = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (deleteR !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status != 200) return this.$message.error(res.meta.msg)
      this.$message.success('')
      this.getroleList()
    },
    //
    async removeRight(role, id2) {
      const removeR = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (removeR !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(
        'roles/' + role.id + '/rights/' + id2
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      role.children = res.data
    },
    // 
    async showSetRightDialog(node) {
      //
      this.role=node
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.getroleRight = res.data
      console.log(node)
      this.getThreekey(node,this.keyArry)
      this.getRightdialogVisible = true
    },
    //id
    getThreekey(node,arr){
        if(!node.children) {
          return arr.push(node.id)
        }
        node.children.forEach(item => {
          this.getThreekey(item,arr)
        });
    },
    //
    async TochangeRight(){
      const arr = [...this.$refs.Thetree.getCheckedKeys(),...this.$refs.Thetree.getHalfCheckedKeys()] 
      const Sarr = arr.join(',')
      const {data:res} = await this.$http.post(`roles/${this.role.id}/rights/`,{rids:Sarr})
      if(res.meta.status!=200) return this.$message.error(res.meta.msg)
      this.$message.success('')
      this.getroleList()
      this.getRightdialogVisible=false
    }
  }
}
</script>
<style lang="less" scoped>
.topline {
  border-top: 1px solid #eee;
}
.bottomline {
  border-bottom: 1px solid #eee;
}
.el-tag {
  margin: 10px;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
