<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-table :data="rightList" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="" prop="level">
          <template slot-scope="scopes">
            <el-tag type="success" v-if="scopes.row.level=='0'"></el-tag>
            <el-tag type="info" v-else-if="scopes.row.level=='1'"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      rightList: []
    }
  },
  created() {
    this.getRightList()
  },
  methods: {
    //
    async getRightList() {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.rightList = res.data
    }
  }
}
</script>

<style lang="less" scoped></style>
