<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20" class="btn">
        <el-col :span="8">
          <el-input v-model="askparams.query" clearable @clear="getUserList">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="dialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!---->
      <el-table :data="userList" border style="width: 100%">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label="" width="180">
        </el-table-column>
        <el-table-column prop="mobile" label="" width="180">
        </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="slotProps">
            <el-switch
              v-model="slotProps.row.mg_state"
              @change="changeuserstate(slotProps.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="solts">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editUser(solts.row.id)"
            ></el-button>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="changeRole(solts.row)"
              ></el-button>
            </el-tooltip>
            <el-button
              icon="el-icon-delete"
              type="danger"
              size="mini"
              @click="deleteUser(solts.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="this.askparams.pagenum"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="this.askparams.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="closeReset"
    >
      <el-form
        :model="addForm"
        :rules="addFormrules"
        ref="addFormref"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editUserReset"
    >
      <el-form
        :model="edituserList"
        :rules="edituserListRules"
        ref="edituserListref"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input v-model="edituserList.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="edituserList.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="edituserList.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="changeRoledialogVisible"
      width="50%"
      @close="clearrole"
    >
      <div>
        <p>:{{ user.username }}</p>
        <br />
        <p>:{{ user.role_name }}</p>
        <br />
        <p>
        <el-select v-model="selectrole" placeholder="">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id"
          >
          </el-option>
        </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeRoledialogVisible = false"> </el-button>
        <el-button type="primary" @click="Tochangerole"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    //
    const checkmobile = (rule, value, callback) => {
      let tocheckM = /^1[34578]\d{9}$/
      if (tocheckM.test(value)) {
        return callback()
      }
      return callback(new Error(''))
    }
    const checkemail = (rule, value, callback) => {
      let tocheckE = /[a-zA-Z0-9]{1,10}@[a-zA-Z0-9]{1,5}\.[a-zA-Z0-9]{1,5}/
      if (tocheckE.test(value)) {
        return callback()
      }
      return callback(new Error(''))
    }
    return {
      askparams: {
        //
        query: '',
        //
        pagenum: 1,
        //
        pagesize: 5
      },
      total: 0,
      userList: [],
      dialogVisible: false,
      editDialogVisible: false,
      edituserList: {},
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      addFormrules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkemail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkmobile, trigger: 'blur' }
        ]
      },
      edituserListRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkemail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkmobile, trigger: 'blur' }
        ]
      },
      //
      changeRoledialogVisible: false,
      //
      user: {},
      //
      roleList: {},
      //
      selectrole:''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    //
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.askparams
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.userList = res.data.users
      this.total = res.data.total
    },
    //
    handleSizeChange(pageSize) {
      this.askparams.pagesize = pageSize
      this.getUserList()
    },
    //
    handleCurrentChange(currentPage) {
      this.askparams.pagenum = currentPage
      this.getUserList()
    },
    //
    async changeuserstate(slotProps) {
      const { data: res } = await this.$http.put(
        `users/${slotProps.id}/state/${slotProps.mg_state}`
      )
      if (res.meta.status !== 200) {
        slotProps.mg_state = !slotProps.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    //
    clear() {
      this.getUserList()
    },
    //
    closeReset() {
      this.$refs.addFormref.resetFields()
    },
    //
    addUser() {
      this.$refs.addFormref.validate(async val => {
        if (!val) return
        //
        const { data: res } = await this.$http.post('users', this.addForm)
        console.log(res)
        if (res.meta.status == 400)
          return this.$message.error('')
        if (res.meta.status !== 201) return this.$message.error('')
        //
        this.dialogVisible = false
        this.$message.success('!')
        this.getUserList()
      })
    },
    //
    async editUser(id) {
      this.editDialogVisible = true
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.edituserList = res.data
    },
    //
    editUserReset() {
      this.$refs.edituserListref.resetFields()
    },
    //
    editUserInfo() {
      this.$refs.edituserListref.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.put(
          'users/' + this.edituserList.id,
          {
            email: this.edituserList.email,
            mobile: this.edituserList.mobile
          }
        )
        console.log(res)
        if (res.meta.status != 200) return this.$message.error('')
        this.editDialogVisible = false
        this.getUserList()
        this.$message.success('')
      })
    },
    //
    async deleteUser(id) {
      const deleteU = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (deleteU !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status != 200) return this.$message.error(res.meta.msg)
      this.$message.success('')
      this.getUserList()
    },
    //
    async changeRole(user) {
      this.changeRoledialogVisible = true
      this.user = user
      //
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status != 200)
        return this.$message.error('')
      this.roleList = res.data
    },
    //
    async Tochangerole(){
      if(!this.selectrole) return this.$message.error('')
      const {data:res} = await this.$http.put(`users/${this.user.id}/role`,{rid:this.selectrole})
      if(res.meta.status!=200) return this.$message.error('')
      this.getUserList()
      this.changeRoledialogVisible=false
    },
    //
    clearrole(){
      this.user=''
      this.selectrole=''
    }
  }
}
</script>
<style lang="less" scoped>
.btn {
  display: flex;
  align-items: center;
}
</style>
