<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column
          label=""
          prop="order_price"
          width="100"
        ></el-table-column>
        <el-table-column label="" prop="is_send" width="100">
          <template slot-scope="solt">
            <el-tag type="success" v-if="solt.row.order_pay == '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="create_time" width="180">
          <template slot-scope="solt">
            {{ solt.row.create_time | dateformat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="130">
          <template>
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="addAddress"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showProgress"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addressVisible"
        width="50%"
        @close="closeAddress"
      >
        <el-form
          :model="addressForm"
          :rules="addressrules"
          ref="addressRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="/" prop="address1">
            <el-cascader
              expand-trigger="hover"
              :options="citydata"
              v-model="addressForm.address1"
            >
            </el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="addressForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addressVisible = false"> </el-button>
          <el-button type="primary" @click="addressVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="progressVisible"
        width="50%"
      >
        <el-timeline >
          <el-timeline-item
            v-for="(activity, index) in progressList"
            :key="index"
            :timestamp="activity.time"
          >
            {{ activity.context}}
          </el-timeline-item>
        </el-timeline>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import citydata from '../../assets/js/citydata.js'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      orderList: [],
      //
      addressVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressrules: {
        address1: [{ required: true, message: '/', trigger: 'blur' }],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      citydata,
      progressVisible: false,
      progressList: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    //
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status != 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      this.total = res.data.total
      this.orderList = res.data.goods
    },
    //
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
      this.getOrderList()
    },
    //
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage
      this.getOrderList()
    },
    //
    addAddress() {
      this.addressVisible = true
    },
    //
    closeAddress() {
      this.$refs.addressRef.resetFields()
    },
    //
    async showProgress() {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status != 200) {
        return this.$message.error('')
      }
      this.progressList = res.data
      this.progressVisible = true
    }
  }
}
</script>
<style lang="less" scoped></style>
