<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="warning"
        :closable="false"
        show-icon
      >
      </el-alert>
      <!--  -->
      <el-row class="top">
        <el-col :span="3">
          <span></span>
        </el-col>
        <el-col :span="5">
          <el-cascader
            expand-trigger="hover"
            v-model="Catechose"
            :options="CateList"
            :props="CatePros"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tab -->
      <!--  -->
      <el-tabs v-model="activeName" type="card" @tab-click="handleCheckClick">
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isbtnDisable"
            @click="adddialogVisible = true"
            ></el-button
          >
          <el-table :data="manyParamsData" style="width: 100%" border>
            <el-table-column type="expand">
              <template slot-scope="scopes">
                <!-- Tag -->
                <el-tag
                  v-for="(item, index) in scopes.row.attr_vals"
                  :key="index"
                  closable
                  @close="handleTagClose(index, scopes.row)"
                >
                  {{ item }}
                </el-tag>
                <!-- Taginput -->
                <el-input
                  class="input-new-tag first"
                  v-if="scopes.row.inputVisible"
                  v-model="scopes.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scopes.row)"
                  @blur="handleInputConfirm(scopes.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scopes.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"> </el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scopes">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="editParams(scopes.row)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteParams(scopes.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isbtnDisable"
            @click="adddialogVisible = true"
            ></el-button
          >
          <el-table :data="onlyParamsData" style="width: 100%" border>
            <el-table-column type="expand">
              <template slot-scope="scopes">
                <!-- Tag -->
                <el-tag
                  v-for="(item, index) in scopes.row.attr_vals"
                  :key="index"
                  closable
                  @close="handleTagClose(index, scopes.row)"
                >
                  {{ item }}
                </el-tag>
                <!-- Taginput -->
                <el-input
                  class="input-new-tag first"
                  v-if="scopes.row.inputVisible"
                  v-model="scopes.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scopes.row)"
                  @blur="handleInputConfirm(scopes.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scopes.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"> </el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scopes">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="editParams(scopes.row)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="deleteParams(scopes.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + Getactive"
      :visible.sync="adddialogVisible"
      width="50%"
      @close="addformClear"
    >
      <el-form
        :model="addform"
        :rules="addformRules"
        ref="addformRef"
        label-width="100px"
      >
        <el-form-item :label="Getactive" prop="attr_name">
          <el-input v-model="addform.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false"> </el-button>
        <el-button type="primary" @click="Toaddform"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="'' + Getactive"
      :visible.sync="editdialogVisible"
      width="50%"
      @close="editformClear"
    >
      <el-form
        :model="editform"
        :rules="editformRules"
        ref="editformRef"
        label-width="100px"
      >
        <el-form-item :label="Getactive" prop="attr_name">
          <el-input v-model="editform.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="Toeditform"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      CateList: [],
      //
      Catechose: [],
      //
      CatePros: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //
      activeName: 'many',
      //
      manyParamsData: [],
      //
      onlyParamsData: [],
      // 
      adddialogVisible: false,
      //
      addform: {
        attr_name: ''
      },
      //
      addformRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //
      editdialogVisible: false,
      //
      editform: {
        attr_name: ''
      },
      //
      editformRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //Tag
      inputVisible: false,
      //input
      inputValue: ''
    }
  },
  created() {
    this.getCatelist()
  },
  computed: {
    //flase
    isbtnDisable() {
      if (this.Catechose.length != 3) {
        return true
      }
      return false
    },
    //id
    GetId() {
      if (this.Catechose.length != 3) {
        return null
      }
      return this.Catechose[2]
    },
    //
    Getactive() {
      if (this.activeName == 'many') {
        return ''
      }
      return ''
    }
  },
  methods: {
    //
    async getCatelist() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status != 200)
        return this.$message.error('')
      this.CateList = res.data
    },
    //
    async handleChange() {
      //
      if (this.Catechose.length != 3) {
        this.Catechose = []
        this.manyParamsData = []
        this.onlyParamsData = []
        return
      }
      this.Togetparams()
    },
    //tab
    handleCheckClick() {
      this.Togetparams()
    },
    //
    async Togetparams() {
      const { data: res } = await this.$http.get(
        `categories/${this.GetId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
        item.inputValue = ''
        item.inputVisible = false
      })
      //
      if (this.activeName == 'many') {
        this.manyParamsData = res.data
      } else {
        this.onlyParamsData = res.data
      }
    },
    addformClear() {
      this.$refs.addformRef.resetFields()
    },
    //
    Toaddform() {
      this.$refs.addformRef.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.post(
          `categories/${this.GetId}/attributes`,
          {
            attr_name: this.addform.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status != 201) return this.$message.error('')
        this.$message.success('')
        this.adddialogVisible = false
        this.Togetparams()
      })
    },
    //
    editformClear() {
      this.$refs.editformRef.resetFields()
    },
    //
    Toeditform() {
      this.$refs.editformRef.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.put(
          `categories/${this.GetId}/attributes/${this.editform.attr_id}`,
          {
            attr_name: this.editform.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status != 200) return this.$message.error('')
        this.$message.success('')
        this.editdialogVisible = false
        this.Togetparams()
      })
    },
    //
    editParams(par) {
      this.editdialogVisible = true
      this.editform = par
    },
    //
    async deleteParams(par) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult != 'confirm')
        return this.$message.info('')
      const { data: res } = await this.$http.delete(
        `categories/${this.GetId}/attributes/${par.attr_id}`
      )
      if (res.meta.status != 200) return this.$message.error('')
      this.$message.success('')
      this.Togetparams()
    },
    //
    handleTagClose(i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAtterVals(row)
    },
    //Tagenter
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length == 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      //
      row.attr_vals.push(row.inputValue)
      row.inputValue = ''
      row.inputVisible = false

      this.saveAtterVals(row)
    },
    //Tag
    showInput(row) {
      row.inputVisible = true
      // 
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    //attr_vals
    async saveAtterVals(row) {
      const { data: res } = await this.$http.put(
        `categories/${this.GetId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(',')
        }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
    }
  }
}
</script>
>
<style lang="less">
.top {
  margin-top: 15px;
  text-align: center;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.el-tag {
  margin: 5px;
}
.input-new-tag.first {
  width: 90px;
  margin-left: 10px;
  display: inline-block;
}
</style>
