<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-alert
        title=""
        type="info"
        center
        show-icon
        :closable="false"
      >
      </el-alert>
      <el-steps
        :space="300"
        :active="activeIndex - 0"
        finish-status="success"
        align-center=""
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="150px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeLeave"
          @tab-click="tabClick"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat" label-width="150px">
              <el-cascader
                expand-trigger="hover"
                :options="cateList"
                v-model="addForm.goods_cat"
                @change="handleChange"
                :props="cateProps"
              >
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              v-for="(item, index) in manyTableData"
              :key="index"
              :label="item.attr_name"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="it"
                  v-for="(it, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              v-for="(item, index) in onlyTableData"
              :key="index"
              :label="item.attr_name"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              class="upload-demo"
              :action="picURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headers"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
             <el-button type='primary' class='addbtn' @click="addCate"></el-button>
          </el-tab-pane>
         
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="previewVisible"
      width="50%"
    >
    <img :src="previewImg" alt="" class="previewImg">
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data() {
    return {
      activeIndex: '0',
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_cat: [],
        pics: [],
        goods_introduce:'',
        attrs:[]
      },
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //
      cateList: [],
      //
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //
      manyTableData: [],
      //
      onlyTableData: [],
      //
      picURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      //
      headers: {
        Authorization: window.sessionStorage.getItem('token')
      },
      //
      previewImg: '',
      //
      previewVisible:false
    }
  },
  created() {
    this.getCateList()
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length == 3) {
        return this.addForm.goods_cat[2]
      } else {
        return null
      }
    }
  },
  methods: {
    //
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status != 200) {
        return this.$message.error('')
      }
      this.cateList = res.data
    },
    //
    handleChange() {
      if (this.addForm.goods_cat.length != 3) {
        this.addForm.goods_cat = []
      }
    },
    //
    beforeLeave(activeName, oldActiveName) {
      if (oldActiveName == '0' && this.addForm.goods_cat.length != 3) {
        this.$message.error('')
        return false
      }
    },
    // 1 
    async tabClick() {
      if (this.activeIndex == '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status != 200) {
          return this.$message.error(res.meta.msg)
        }
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.split(',')
        })
        this.manyTableData = res.data
        console.log(this.manyTableData)
      } else if (this.activeIndex == '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status != 200) {
          return this.$message.error('')
        }
        console.log(res.data)
        this.onlyTableData = res.data
      }
    },
    //
    handlePreview(data) {
      console.log(data)
      this.previewImg = data.response.data.url
      this.previewVisible = true
    },
    //
    handleRemove(data) {
      var i = this.addForm.pics.findIndex(
        x => x.pic == data.response.data.tmp_path
      )
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    //
    handleSuccess(response) {
      var picInfo = {
        pic: response.data.tmp_path
      }
      this.addForm.pics.push(picInfo)
    },
    //
    addCate(){
      this.$refs.addFormRef.validate(
      async  val=>{
          if(!val){
            return this.$message.error('')
          }
          var form = _.cloneDeep(this.addForm)
          form.goods_cat = form.goods_cat.join(',')
          console.log(this.manyTableData)

          //
          this.manyTableData.forEach(item=>{
            var newAttr = {
              attr_id:item.attr_id,
              attr_value:item.attr_vals.join(' ')
            }
            this.addForm.attrs.push(newAttr)
          })

          //
          this.onlyTableData.forEach(item=>{
            var newAttr = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals
            }
            this.addForm.attrs.push(newAttr)
          })
          form.attrs = this.addForm.attrs
          const { data:res } = await this.$http.post('goods',form)
          if(res.meta.status!==201){
             return this.$message.error(res.meta.msg)
          }
          this.$message.success('')
          this.$router.push('/home/goods')
        })
    }
  }
}
</script>
<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.previewImg{
  width:100%;
  height:100%;
}
.addbtn{
  margin-top:15px;
}

</style>
