/* eslint-disable vue/no-unused-vars */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20" class="btn">
        <el-col :span="8">
          <el-input placeholder="" clearable @clear='getGoodsList' v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addPage"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="goodList" border>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="goods_name" label=""> </el-table-column>
        <el-table-column prop="goods_price" label="" width="120">
        </el-table-column>
        <el-table-column prop="goods_number" label="" width="80">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="80">
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="slot">
            {{ slot.row.add_time | dateformat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="slot">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteById(slot.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      goodList: [],
      //
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    //
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status != 200) {
        return this.$message.error('' + res.meta.msg)
      }
      this.$message.success('')
      this.goodList = res.data.goods
      this.total = res.data.total
    },
    //
    handleSizeChange(newsize){
      this.queryInfo.pagesize = newsize
      this.getGoodsList()
    },
    //
    handleCurrentChange(newnum){
        this.queryInfo.pagenum = newnum
        this.getGoodsList()
    },
    //
    async deleteById(id){
       const result = await  this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)

      if(result!='confirm'){
        return this.$message.info('!')
      }
     const {data:res} =  await this.$http.delete(`goods/${id}`)
     if(res.meta.status!=200){
       return this.$message.error('')
     }
      this.$message.success('')
      this.getGoodsList()
    },
    //
    addPage(){
      this.$router.push('/home/goods/add')
    }
  }
}
</script>
<style lang="less" scoped></style>
