<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCatedialogVisible"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        :data="cateList"
        :columns="columns"
        show-index
        index-text="#"
        :show-row-hover="false"
        :expand-type="false"
        :selection-type="false"
        border
        style="font-size:14px; space-letter:2px;margin-top:15px"
      >
        <!--  -->
        <template slot="isok" slot-scope="scopes">
          <i
            class="el-icon-success"
            v-if="scopes.row.cat_deleted === false"
            style="font-size:15px;color:green"
          ></i>
          <i class="el-icon-error" style="color:red;" v-else></i>
        </template>

        <!--  -->
        <template slot="grade" slot-scope="scopes">
          <el-tag v-if="scopes.row.cat_level == '0'" size="mini"></el-tag>
          <el-tag
            v-else-if="scopes.row.cat_level == '1'"
            type="success"
            size="mini"
            ></el-tag
          >
          <el-tag v-else type="warning" size="mini"></el-tag>
        </template>

        <!--  -->
        <template slot="btn" slot-scope="scopes">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="editTheCate(scopes.row)"
            ></el-button
          >
          <el-button
            icon="el-icon-delete"
            type="danger"
            size="mini"
            @click="deleteCate(scopes.row)"
            ></el-button
          >
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="catedata.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="catedata.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCatedialogVisible"
      width="50%"
      @close="clearCate"
    >
      <el-form
        :model="addCate"
        :rules="addCateRules"
        ref="addCateRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCate.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- option -->
          <el-cascader
            expand-trigger="hover"
            v-model="parentArry"
            :options="parentCateList"
            :props="parentCateCascader"
            @change="parentCatechange"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCatedialogVisible = false"> </el-button>
        <el-button type="primary" @click="ToaddCate"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editcatedialogVisible"
      width="50%"
    >
      <el-form
        :model="editCate"
        :rules="addCateRules"
        ref="editCateRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editCate.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editcatedialogVisible = false"> </el-button>
        <el-button type="primary" @click="ToeditCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      cateList: [],
      //
      total: 0,
      //
      catedata: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      //table
      columns: [
        {
          label: '',
          prop: 'cat_name',
          width: '400px'
        },
        {
          label: '',
          type: 'template',
          template: 'isok'
        },
        {
          label: '',
          type: 'template',
          template: 'grade'
        },
        {
          label: '',
          type: 'template',
          template: 'btn'
        }
      ],
      //
      addCatedialogVisible: false,
      //
      editcatedialogVisible: false,
      //
      addCate: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      //
      addCateRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //
      parentCateList: [],
      //
      parentArry: [],
      //
      parentCateCascader: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //
      editCate: {}
    }
  },
  created() {
    this.getcateList()
  },
  methods: {
    //
    async getcateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.catedata
      })
      if (res.meta.status != 200) return this.$message.error('')
      this.total = res.data.total
      this.cateList = res.data.result
    },
    //
    handleSizeChange(size) {
      this.catedata.pagesize = size
      this.getcateList()
    },
    //
    handleCurrentChange(Csize) {
      this.catedata.pagenum = Csize
      this.getcateList()
    },
    //
    showAddCatedialogVisible() {
      this.addCatedialogVisible = true
      this.getParentList()
    },
    //
    async getParentList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.parentCateList = res.data
    },
    //
    parentCatechange() {
      if (this.parentArry.length > 0) {
        this.addCate.cat_pid = this.parentArry[this.parentArry.length - 1]
        this.addCate.cat_level = this.parentArry.length
      } else {
        this.addCate.cat_pid = 0
        this.addCate.cat_level = 0
      }
    },
    //
    ToaddCate() {
      this.$refs.addCateRef.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.post('categories', this.addCate)
        if (res.meta.status != 201) return this.$message.error('')
        this.$message.success('')
        this.getcateList()
        this.addCatedialogVisible = false
      })
    },
    //
    clearCate() {
      this.$refs.addCateRef.resetFields()
      this.addCate.cat_pid = 0
      this.addCate.cat_level = 0
      this.parentArry = []
    },
    //
    async editTheCate(role) {
      this.editcatedialogVisible = true
      const { data: res } = await this.$http.get('categories/' + role.cat_id)
      if (res.meta.status != 200)
        return this.$message.error('')
      this.editCate = res.data
    },
    //
    async ToeditCate() {
      this.$refs.editCateRef.validate(async val => {
        if (!val) return
        const { data: res } = await this.$http.put(
          'categories/' + this.editCate.cat_id,
          { cat_name: this.editCate.cat_name }
        )
        if (res.meta.status != 200) return this.$message.error('!')
        this.editcatedialogVisible = false
        this.$message.success('')
        this.getcateList()
      })
    },
    //
    async deleteCate(role) {
      const deleteC = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(error => error)
      if(deleteC != 'confirm') return this.$message.info('')

      const {data:res} =await this.$http.delete('categories/'+role.cat_id)
      if(res.meta.status!=200) return this.$message.error('!')
      this.$message.success('!')
      this.getcateList()
    }
  }
}
</script>

<style lang="less">
.el-cascader {
  width: 100%;
}
</style>
